"use client";

import { useState } from "react";

interface AuthModalProps {
    isOpen: boolean;
    onClose: () => void;
    onSuccess?: (user: { email: string }) => void;
}

export default function AuthModal({ isOpen, onClose, onSuccess }: AuthModalProps) {
    const [email, setEmail] = useState("");
    const [code, setCode] = useState("");
    const [step, setStep] = useState<"email" | "code">("email");
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState<string | null>(null);

    if (!isOpen) return null;

    const handleSendCode = async () => {
        if (!email.trim()) {
            setError("Please enter your email");
            return;
        }

        setLoading(true);
        setError(null);

        try {
            const res = await fetch("/api/auth/send-code", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({ email: email.trim() })
            });
            const data = await res.json();

            if (data.ok) {
                setStep("code");
            } else {
                setError(data.message || "Failed to send code");
            }
        } catch (err) {
            setError("Something went wrong. Please try again.");
        } finally {
            setLoading(false);
        }
    };

    const handleVerifyCode = async () => {
        if (!code.trim()) {
            setError("Please enter the code");
            return;
        }

        setLoading(true);
        setError(null);

        try {
            const res = await fetch("/api/auth/verify-code", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({ email: email.trim(), code: code.trim() })
            });
            const data = await res.json();

            if (data.ok) {
                onSuccess?.({ email: email.trim() });
                onClose();
                // Reload page to get updated session
                window.location.reload();
            } else {
                setError(data.message || "Invalid code");
            }
        } catch (err) {
            setError("Something went wrong. Please try again.");
        } finally {
            setLoading(false);
        }
    };

    const handleClose = () => {
        setEmail("");
        setCode("");
        setStep("email");
        setError(null);
        onClose();
    };

    return (
        <div
            className="fixed inset-0 bg-black/50 flex items-center justify-center z-[1000] p-5"
            onClick={(e) => e.target === e.currentTarget && handleClose()}
        >
            <div className="bg-white rounded-2xl shadow-modal w-full max-w-[400px] p-8 relative">
                <button
                    onClick={handleClose}
                    aria-label="Close"
                    className="absolute top-4 right-4 text-gray-400 hover:text-gray-900 transition-colors p-2"
                >
                    <svg width="20" height="20" viewBox="0 0 20 20" fill="none">
                        <path d="M15 5L5 15M5 5L15 15" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" />
                    </svg>
                </button>

                <div className="text-center mb-6">
                    <h2 className="font-display text-xl font-bold text-gray-900 mb-2">
                        {step === "email" ? "Sign in or create account" : "Check your email"}
                    </h2>
                    <p className="text-gray-500 text-sm">
                        {step === "email"
                            ? "We'll send you a login code. No password needed."
                            : `We sent a 6-digit code to ${email}`}
                    </p>
                </div>

                {step === "email" ? (
                    <div className="space-y-4">
                        <input
                            type="email"
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            placeholder="you@example.com"
                            className="input"
                            autoFocus
                            onKeyDown={(e) => e.key === "Enter" && handleSendCode()}
                        />

                        {error && (
                            <div className="text-red-600 text-sm text-center">{error}</div>
                        )}

                        <button
                            onClick={handleSendCode}
                            disabled={loading}
                            className="btn-primary w-full"
                        >
                            {loading ? "Sending..." : "Send Code"}
                        </button>
                    </div>
                ) : (
                    <div className="space-y-4">
                        <input
                            type="text"
                            value={code}
                            onChange={(e) => setCode(e.target.value.replace(/\D/g, "").slice(0, 6))}
                            placeholder="000000"
                            className="input text-center text-2xl tracking-widest font-mono"
                            autoFocus
                            onKeyDown={(e) => e.key === "Enter" && handleVerifyCode()}
                        />

                        {error && (
                            <div className="text-red-600 text-sm text-center">{error}</div>
                        )}

                        <button
                            onClick={handleVerifyCode}
                            disabled={loading || code.length !== 6}
                            className="btn-primary w-full"
                        >
                            {loading ? "Verifying..." : "Verify Code"}
                        </button>

                        <button
                            onClick={() => { setStep("email"); setCode(""); setError(null); }}
                            className="btn-ghost w-full text-sm"
                        >
                            Use a different email
                        </button>
                    </div>
                )}

                <p className="text-center text-xs text-gray-500 mt-4">
                    By continuing, you agree to our Terms and Privacy Policy.
                </p>
            </div>
        </div>
    );
}
